import React, { Component } from 'react';
import PropTypes from 'prop-types'
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import NativeSelect from '@mui/material/NativeSelect';
import OneAPI from '../../api/OneAPI';


export class AktivittenSelection extends Component {
  constructor(props) {
    super(props);

    // Init state
    this.state = {
      selectedAktivitt: '',
      aktivitten: []
    };
  }

  componentDidMount() {
    this.loadAktivitten()
  }

  loadAktivitten = () => {
    OneAPI.getAPI().getAktivittenByProjectId(this.props.project).then(aktivitten =>
      this.setState({
        aktivitten: aktivitten,
        loadingInProgress: false, // loading indicator 
        loadingError: null
      })).catch(e =>
        this.setState({ // Reset state with error from catch 
          loadingInProgress: false,
          loadingError: e
        })
      );

    // set loading to true
    this.setState({
      loadingInProgress: true,
      loadingError: null
    });
  }
  /** gets the balance for this account */


  handleChange = (event) => {
    this.setState({
      selectedAktivitt: event.target.value
    })

    setTimeout(() => {
        if(this.state.selectedAktivitt){
      this.props.handleSelection(this.state.selectedAktivitt);
    }}, 300);
	}


  render() {
    const {} = this.props;
    const {selectedAktvitt, aktivitten} = this.state;
    return (
      <div>
        <div>
      <FormControl sx={{ m: 1, minWidth: 120 }}>
        <InputLabel variant="standard" htmlFor="uncontrolled-native">
          Aktvitt
        </InputLabel>
        <NativeSelect
          defaultValue={0}
          inputProps={{
            name: 'aktivitten',
            id: 'uncontrolled-native',
          }}
          onChange={this.handleChange}
        >
            <option value={0}></option>
           {aktivitten ?
          aktivitten.map((aktivitt, index) => <option value={aktivitt.id}>{aktivitt.getBezeichnung()}</option>)
          :null}
        </NativeSelect>
      </FormControl>
        </div>
    </div>
    )
  }
}

AktivittenSelection.propTypes = {
    handleSelection: PropTypes.any,
    project: PropTypes.any
}

export default AktivittenSelection
